<template>
  <div class="Discussion col-lg-4 p-0 ml-auto" v-if="isShowDiscussion">
    <header>
      <div class="text-center">
        <h1>Discussion</h1>
      </div>
      <button class="btn Hide-Box_Discussion" @click="hideDiscussion">
        <i class="fa fa-arrow-right"></i>
      </button>
    </header>

    <div class="Discussion-Container pt-1 px-2">
      <h3 class="text-center my-2">#_{{serveur.identifiant}}</h3>

      <!--User message-->
      <div v-for="(messageItem, key) in _allMessages" :key="key">
        <BoxMessage
        :message="messageItem.message"
        :sender="messageItem.expand.user_id.name"
        :profil="messageItem.expand.user_id.profil"
        :userId="messageItem.expand.user_id.id"
        ></BoxMessage>
        <!-- End User message-->
      </div>
    </div>

    <div class="Send-File_visualisation row m-0 p-2">
      <!--file to sende-->
    </div>
    <footer class="px-2">
      <div class="Select-file">
        <label for="Find-File" role="button"
          ><i class="fa fa-file-import"></i
        ></label>
        <input type="file" id="Find-File" style="display: none" />
      </div>
      <input
        type="text"
        class="Text-Message"
        v-model="message"
        placeholder="Message..."
        @keydown="handleSend"
      />
      <!-- <button class="btn Sender-message"></button> -->
    </footer>
  </div>
  <button
    class="Show-Box_Discussion"
    v-if="!isShowDiscussion"
    @click="showDiscussion"
  >
    <div v-if="notification != 0">
      <span>{{ _notification }}</span>
    </div>
    <img src="@/assets/icon/Speech.png" alt="" srcset="" />
  </button>
</template>

<script setup>
import BoxMessage from "./message/BoxMessage.vue";
</script>
<script>
import PocketBase from "pocketbase";
import store from "@/store";
const pb = new PocketBase("http://127.0.0.1:8090");

export default {
  name: "BoxDiscussion",
  computed: {
    _allMessages() {
      this.notification += 1;
      return this.$store.getters.getMessage;
    },
    _notification() {
      return this.notification;
    },
  },
  data() {
    return {
      isShowDiscussion: true,
      message: "",
      roomId: "",
      allMessage: [],
      notification: 0,
      discussionScroller: [],
      serveur:[]
    };
  },
  async mounted() {
    /* eslint-disable */
    /**************** */
    let roomId = this.$route.params.roomId;
    this.roomId = roomId;
    let recordAllMessages = await pb.collection("message").getList(1, 50, {
      filter: `room_id = "${roomId}"`,
      expand: "user_id",
    });
    store.commit("storeMessages", recordAllMessages.items);

    pb.collection("room").subscribe(roomId, async function (e) {
      let message = e.record.new_message;
      let newMessage = await pb.collection("message").getOne(`${message}`, {
        expand: "user_id",
      });
      store.commit("pushMessage", newMessage);
    });
    this.serveur = await pb.collection('room').getOne(`${roomId}`, {
    
});
    this.scrollDown();
  },
  methods: {
    hideDiscussion() {
      this.isShowDiscussion = false;
    },
    scrollDown() {
      let box = document.querySelector(".Discussion-Container");
      this.discussionScroller = box;
      this.discussionScroller.scrollTop =
        this.discussionScroller.scrollHeight + 40;
    },

    showDiscussion() {
      this.isShowDiscussion = true;
      this.notification = 0;
    },
    async handleSend(e) {
      if (e.key == "Enter" && this.message != "") {
        const data = {
          message: this.message,
          user_id: this.$store.getters.getConnexion.authStore.model.id,
          room_id: this.roomId,
        };
        console.log(data);
        let message = await this.$store.getters.getConnexion
          .collection("message")
          .create(data);
        let roomData = await this.$store.getters.getConnexion
          .collection("room")
          .getOne(`${this.roomId}`, {});
        roomData.messages.push(message.id);
        roomData.new_message = message.id;
        await this.$store.getters.getConnexion
          .collection("room")
          .update(`${this.roomId}`, roomData);
        this.message = "";
        this.scrollDown();
        return;
      }
    },
  },
};
</script>
