<template>
  <header
    class="d-flex justify-content-between align-items-center pt-3 px-4 Navbar_"
  >
  <h1 class="NavBar_Logo">Hi-zara.<span class="domain-name">mg</span></h1>
    <div class="NavBar_ButtonLogin my-auto d-flex align-items-center">
        <!-- <GoogleLogin :callback="callback">
            <button>Login Using Google</button>
        </GoogleLogin> -->
      <div v-if="isAuth" class="Navbar_UserProfil px-5">
        <div class="user-profil">
          <img v-if="this.$store.getters.getConnexion.authStore.model.profil !=''" :src="this.$store.getters.getConnexion.authStore.model.profil" alt="user-profil" >
        </div>
        <div v-if="this.$store.getters.getConnexion.authStore.model.profil =='' " class="profil-configuration">
        <label for="profil"><i class="fa fa-plus"></i></label>
          <input type="file" @change="addprofile" name="profil" id="profil">
        </div>
        <div class="user-name">{{ this.$store.getters.getConnexion.authStore.model.name }}</div>
      </div>
      <RouterLink v-if="!isAuth" class="btn btn-dark" to="login/sign-in">Se connecter</RouterLink>

      <button title="se deconnecter" v-if="isAuth" class="btn btn-dark" @click="logout"><i class="fa fa-sign-out-alt "></i></button>
      <!-- <button @click="create">create</button> -->
      <!--  <GoogleLogin :params="params" :logoutButton=true>Logout</GoogleLogin>-->
      <p></p>
    </div>
  </header>
</template>

<script>


// import { decodeCredential } from 'vue3-google-login'
// import store from "@/store";// eslint-disable-next-line
import axios from 'axios'
export default {
    name: 'NavBar',
    data(){
        return{
            isAuth:true
        }
     },
    setup() {
        const callback = (response) => {
        // const userData = decodeCredential(response.credential)
        console.log("Handle the response", response)
        }
        return {
            callback
        }
    },
    created:function(){
     if (this.$store.getters.getConnexion.authStore.isValid===false) {
      this.isAuth = false
     }else{
      this.isAuth = this.$store.getters.getConnexion.authStore.isValid;
     }
     console.log(this.isAuth);
       
        // await this.$store.getters.getConnexion.collection('users').authWithOAuth2({ provider: 'google' })
        
    },
    methods: {
      addprofile(e){
        console.log(e.target);
        let fileData = e.target.files[0];
        let profile = new FormData()
        profile.append('file', fileData)
        axios.post(process.env.VUE_APP_SHARE_FILE_ENDPOINT, profile).then(async(response) => {
                let data = response.data
                let test = await this.$store.getters.getConnexion.collection('users').update(this.$store.getters.getConnexion.authStore.model.id, 
                {"profil":data.url}
                )
                console.log(test);
            })
      },
        handleUser(){
             console.log(this.$store.state.user);
             
        },
        async logout(){
           await this.$store.getters.getConnexion
           .authStore.clear();
           this.$router.push('/login/sign-in')
        }
        ,
        onSuccess(googleUser) {
            console.log(googleUser);
            console.log(googleUser.getBasicProfile());
        }
    }
}
</script>
