<template>
    <div class="login container mt-5 pt-5">
        <div class="content mt-5">
            <h1 class="mt-5">Hi-zara.<span class="domain-name">mg</span></h1>
            <p class="mb-3 d-inline">Vous n'avez pas encore une compte ? <span class="text-primary">
                    <RouterLink to="/login/sign-up">S' enregistrer</RouterLink>
                </span></p>
            <form action="" class="w-50 mt-3">
                <div v-if="messageError" class="alert alert-danger my-2 w-75">
                <strong>Erreur !</strong> {{ messageError }}
            </div>
                <div class="form-group ">
                    <label class="form-label" for="">Email</label>
                    <input required @change="handleChange" v-model="email" type="email" class="form-control w-75" placeholder="Email ...">
                </div>
                <div class="form-group mt-3">
                    <label class="form-label" for="">Mot de passe</label>
                    <input required  @change="handleChange" v-model="password" type="password" class="form-control w-75" placeholder="Mot de passe ...">
                </div>

                <button role="submit" type="button" @click="handleSubmit" class="btn  btn-dark w-75 mt-5">Se connecter</button>
            </form>
        </div>
    </div>
</template>

<script>

export default {
    data() {
        return {
            email: "",
            password: "",
            messageError: ""
        }
    },
    methods: {
        handleChange(){
            this.messageError = ""
        }
        ,
        async handleSubmit(e) {
            e.preventDefault()
            let response = this.$store.getters.getConnexion.collection('users').authWithPassword(
            this.email,
            this.password,
            );
            response.then((response)=>{
                console.log(response);
                if (this.$store.getters.getConnexion.authStore.isValid) {
                this.$router.push('/')
            }
            })
            .catch(()=>{
                this.messageError ="Email ou mot de passe incorrect, veuillez insérer la bonne credentiel ! "
            })
//             console.log(this.$store.getters.getConnexion.authStore.isValid);
// console.log(this.$store.getters.getConnexion.authStore.token);
// console.log(this.$store.getters.getConnexion.authStore.model);
            
        }
    }

}
</script>