import { createStore } from "vuex";
import PocketBase from "pocketbase";
const pb = new PocketBase('http://127.0.0.1:8090')

/* eslint-disable */
const store = createStore({
    state:{
        page:1,
        messages:[],
        pb: pb,
        pdf:"url"
        
    },
    mutations:{
        storeMessages(state, message){
            state.messages = message
        },
        storeCurentPage(state, page){
            state.page = page
        },
        pushMessage(state, newMessage){
            state.messages.push(newMessage)
        }
    },
    actions:{
        storeMessages({commit}, message){
            store.commit("storeMessages",message)
        },
        storeCurentPage({commit}, page){
            store.commit("storeCurentPage",page)
        },
        pushMessage({commit}, newMessage){
            store.commit("pushMessage",newMessage)
        }
    },
    getters:{
        getMessage(state){
            return state.messages;
        },
        getPage(state){
            return parseInt(state.page)
        },
        getConnexion(state){
            return state.pb
        }
    }
    ,
    
    modules:{

    }
})

export default store