<template>
  <div class="login container mt-5 pt-5">
    <div class="content mt-5">
      <h1 class="mt-5">Hi-zara.<span class="domain-name">mg</span></h1>
      <p class="mb-3">
        Vous avez déjà une compte ?
        <span class="text-primary"
          ><RouterLink to="sign-in">Se connecter</RouterLink></span
        >
      </p>
      <form action="" class="w-50 mt-3">
        <div class="form-group">
          <label class="form-label" for="">Nom</label>
          <input
          required
            v-model="name"
            type="text"
            class="form-control w-75"
            placeholder="Nom ..."
          />
        </div>
        <div class="form-group mt-3">
          <label class="form-label" for="">Email</label>
          <input
          required
            v-model="email"
            type="email"
            class="form-control w-75"
            placeholder="Email ..."
          />
          <small class="text-danger">{{ messageErrorEmail }}</small>
        </div>
        <div class="form-group mt-">
          <label class="form-label" for="">Mot de passe</label>
          <input
          required
            minlength="8"
            v-model="password"
            type="password"
            class="form-control w-75"
            placeholder="Mot de passe a 8 caractére"
          />
          <small class="invalid-feedback">Mot de passe trop cour </small>
        </div>
        <button
          role="submit"
          @click="handleSubmit"
          class="btn btn-dark w-75 mt-5"
        >
          S' enregistrer
        </button>
      </form>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      name: "",
      email: "",
      password: "",
      messageErrorEmail: "",
    };
  },
  methods: {
    async handleSubmit(e) {
       e.preventDefault()
      const data = {
        username: "",
        email: this.email,
        emailVisibility: true,
        password: this.password,
        passwordConfirm: this.password,
        name: this.name,
      };
      let response =  this.$store.getters.getConnexion
        .collection("users")
        .create(data);
        response.then(()=>{
          this.$store.getters.getConnexion
        .collection("users")
        .requestVerification(this.email);
        this.$router.push('/login/sign-in')
        })
        .catch(()=>{
          this.messageErrorEmail = "Cette email est déjà prise , veuillez utilisez un autre adresse !"
        })
     
    },
  },
};
</script>
