<template>
   <NavBarVue></NavBarVue>
   <section class="row container d-flex mx-auto mt-5 CreationJoinServer_">
       <div class="col-md-6 CreationJoinServer_JoinServer">
        <div v-if="error" class="error alert-danger p-2 mb-2">cette serveur n'existe pas dans le plateforme, veuillez inseré le bon indentifiant ou mot de passe</div>
            <h1>
                Joindre un serveur diffusion
            </h1>
            <form action="" class="mt-4">
                <div class="form-group">
                    <label for="">Identifiant du serveur</label>
                   <div class="validation-server-id"> <input type="text" class="form-control" v-model="idServer" placeholder=" Exemple: Fz45tl"> <i v-if="!_server" class="fa fa-sync fa-spin text-danger"></i><i v-if="_server" class="fa fa-check-circle text-success"></i></div>
                </div>
                <div class="form-group" v-if="hasPassword">
                    <label for="">Mot de passe</label>
                    <input type="text" v-model="password"  class="form-control" placeholder="mot de passe">
                </div>
                <button type="button" @click="handleJoinServer" class="btn Joindre mt-3">Joindre <i class="fa fa-arrow-right ml-2"></i></button>
               
            </form>
       </div>
       <!-- <div class="col-md-6 ServerTest"></div> -->
   </section>
</template>

<script>
import NavBarVue from '@/components/NavBar.vue';
import PocketBase from 'pocketbase';
const pb = new PocketBase('http://127.0.0.1:8090');

export default { 
    name:'JoinServerView',
    components:{
        NavBarVue
    },
    computed:{
        _server(){
            
            this.checkServer()
            return this.verify
        },
    },
    data(){
        return{
            error:false,
            hasPassword:true,
            verify:false,
            idServer:"",
            password:"",
            serverInformation:undefined
        }
    },
    methods:{
        checkServer(){
            let server = pb.collection('room').getFirstListItem(`identifiant="${this.idServer}"`)
            server
            .then((result)=>{
                this.serverInformation = result
                if(result.password==""){
                    this.hasPassword =false
                }
                this.hasPassword = true
                this.verify = true
                return result
            })
            .catch(()=>{
                this.verify =false
                this.serverInformation = undefined
            })
        },
        handleJoinServer(){
            let server = pb.collection('room').getFirstListItem(`identifiant="${this.idServer}" && password="${this.password}"`)
            server
            .then((result)=>{
                this.error = false
                this.$router.push(`/diffusion/${result.id}/${result.identifiant}`)
            })
            .catch(()=>{
                this.error = true
            })
        }
    }
    

}

</script>