<template>
  <div class="Diffusion-Container row m-0 p-2">
    <div class="col-lg-6 document-viewer">
      <vue-pdf-embed :source="pdfUrl" :page="_curentPage" width="550" />
      <div class="control">
        <button @click="previousPage" class="previous-page">
          <i class="fa fa-angle-left"></i>
        </button>
        <button @click="nextPage" class="next -page">
          <i class="fa fa-angle-right"></i>
        </button>
      </div>
      <div class="action">
        <a role="button" :href="pdfUrl" class="btn btn-primary">
          <i class="fa fa-arrow-down mr-2"></i> Télécharger
        </a>
      </div>
    </div>

    <BoxDiscussion></BoxDiscussion>
  </div>
</template>
<script setup>
import VuePdfEmbed from "vue-pdf-embed";
</script>
<script>
import BoxDiscussion from "@/components/BoxDiscussion.vue";
import store from "@/store";

export default {
  name: "DiffusionView",
  components: {
    BoxDiscussion,
  },
  computed: {
    _curentPage() {
      console.log("called");
      console.log(this.$store.getters.getPage);
      return this.$store.getters.getPage;
    },
    _room() {
      return this.room;
    },
  },
  data() {
    return {
      realtimeConnection: null,
      room: "",
      isShowDiscussion: Boolean,
      pdfUrl: "http://127.0.0.1:3001/api/download/a2beb442-97ce-4824-8648-7ce438698b3820-mots-pour-enrichir-son-vocabulaire-professionnel-4.pdf",
    };
  },
  methods: {
    hideDiscussion() {
      this.isShowDiscussion = false;
    },
    async nextPage() {
      let curentPage = this.$store.getters.getPage + 1;
      store.commit("storeCurentPage", curentPage);
      let data = {
        action: "currentpage",
        room: this._room,
        page: curentPage,
      };
      await this.connection.send(JSON.stringify(data));
    },
    async previousPage() {
      if (this.$store.getters.getPage > 1) {
        let curentPage = this.$store.getters.getPage - 1;
        store.commit("storeCurentPage", curentPage);
        let data = {
          action: "currentpage",
          room: this._room,
          page: curentPage,
        };
        await this.connection.send(JSON.stringify(data));
      }
    },
  },
  created: function () {
    let ws = new WebSocket("ws://127.0.0.1:5500");
    this.connection = ws;

    let roomId = this.$route.params.roomId;
    this.room = roomId;
    let data = {
      action: "configuration",
      room: roomId,
    };
    this.connection.onmessage = function (event) {
      let page = parseInt(event.data);
      store.commit("storeCurentPage", page);
    };

    this.connection.onopen = function () {
      ws.send(JSON.stringify(data));
    };
  },
  // created: function () {

  // }
};
</script>
