<template>
    <section class="row m-0 m-5 HomeShareFile_">
        <div class="col-lg-6 HomeShareFile_Ullustration">
            <img src="@/assets/img/croods.png" alt="">
        </div>
        <div class="col-lg-6 HomeShareFile_Detail">
            <h2>Partagez vos fichiers en toute securité</h2>
            <div v-if="FileOpenOnBrowser" class="row">
                <div class="mt-3 HomeShareFile_Detail-Confirmation">
                    <div class="py-2 HomeShareFile_ContainerIconFile ">
                        <button @click="HandleRemoveFile"><i class="fa fa-times-circle "></i></button>
                        <img class="mx-auto d-block" src="@/assets/icon/File.png" alt="File upload ">
                    </div>
                    <p class="mt-1"> {{ _File.get('file')['name'] }}</p>
                </div>
            </div>
            <button v-if="FileOpenOnBrowser" @click="HandleToUpload" class="btn btn-primary mt-2">Partager</button>
            <form class=" mt-4 d-flex  align-content-center justify-content-center  HomeShareFile_FileDrop" action="">
                <label for="FileShare">Glisé & Déposé </label> &nbsp;
                <input type="file" name="fileShare" id="FileShare" @change="HandleChange" />
                <span class="text-dark" v-if="FileOpenOnBrowser">{{ FileSize }} Mo </span>
            </form>
        </div>
        <div v-if="Uploaded" id="overflow">
            <button class="btn" @click="Uploaded = false"><i class="fa fa-times"></i></button>
            <h2 class="mb-4">Lien de téléchargement</h2>
            <input type="text" class="form-control mb-2" v-bind:value="_DownloadFile">
            <button @click="copyLink" class="btn btn-primary mt-2">copiez !</button>
        </div>
    </section>
</template>


<script>

import axios from 'axios'
import PocketBase from 'pocketbase';

export default {
    name: 'HomeShareFile',
    computed: {
        _File() {
            return this.File
        },
        _DownloadFile() {
            return this.DownloadFile
        }
    }
    ,
    data() {
        return {
            FileOpenOnBrowser: false,
            File: new FormData(),
            FileSize: 0,
            Uploaded: false,
            DownloadFile: "",
        }
    },
    methods: {
        copyLink(){
            navigator.clipboard.writeText(this._DownloadFile)
            this.FileOpenOnBrowser = false
        },
        HandleChange(e) {
            this.FileOpenOnBrowser = true;
            let fileData = e.target.files[0];
            let convertFileToMegaOcte = (fileData.size / 1024) / 1024;
            this.FileSize = convertFileToMegaOcte.toFixed(2);
            this.File.set('file', fileData)
        },
        HandleRemoveFile() {
            this.File.delete('file');
            this.FileOpenOnBrowser = false

        },
        async HandleToUpload() {
            const pb = new PocketBase('http://127.0.0.1:8090')

            this.Uploaded = true
            let options = {
                headers: {
                    ' Access-Control-Allow-Origin': process.env.VUE_APP_SHARE_FILE_URL,
                    'Cross-Origin': 'Same Origin'
                },
                onUploadProgress: progressEvent => {
                    const { loaded, total } = progressEvent;
                    let percent = Math.floor((loaded * 100) / total)
                    if (percent < 100) {
                        console.log(`loaded ${percent} %`);
                    }
                }
            }
            axios.post(process.env.VUE_APP_SHARE_FILE_ENDPOINT, this.File, options).then(async(response) => {
                let data = response.data
                this.DownloadFile = data.url
                let upload = await pb.collection('file').create({
                    name:data.name,
                    mimetype: data.mimetype,
                    size: data.size,
                    url: data.url,
                    user: this.$store.getters.getConnexion.authStore.model.id
                })
                console.log(upload);
                this.FileOpenOnBrowser = false
            })
        },
    }

}
</script>