<template>
  <NavBarVue></NavBarVue>
  <section class="row container d-flex mx-auto mt-5 CreationJoinServer_">
    <div class="col-md-6 CreationJoinServer_JoinServer">
      <h1>Serveur de diffusion</h1>
      <div v-if="message" class="alert-success p-4">
        Le serveur est crée avec succées !
      </div>
      <form action="" class="mt-4">
        <div class="form-group">
          <label for="">Identifiant du serveur</label>
          <input
            v-model="room"
            type="text"
            class="form-control"
            placeholder="example:Room-L3"
          />
        </div>
        <div class="form-group">
          <label for="">Mot de passe</label>
          <input
            v-model="password"
            type="text"
            class="form-control"
            placeholder="mot de passe"
          />
        </div>
        <!-- <div class="form-check">
          <label class="form-check-label">
            <input
              type="checkbox"
              class="form-check-input"
              value=true
              
            />Discussion instantané 
          </label>
        </div> -->
        <button v-if="!created" @click="createServer" class="btn Joindre mt-3">
          Creer <i class="fa fa-arrow-right ml-2"></i>
        </button>
        <button v-if="created" @click="joinServeur" class="btn Joindre mt-3">
          joindre <i class="fa fa-arrow-right ml-2"></i>
        </button>
      </form>
    </div>
    <!-- <div class="col-md-6 ServerTest"></div> -->
  </section>
</template>

<script>
import NavBarVue from "@/components/NavBar.vue";

export default {
  name: "CreationServerView",
  components: {
    NavBarVue,
  },
  data() {
    return {
      room: "",
      password: "",
      created:false,
      message:false,
      serveur:""
    };
  },
  methods:{
    createServer(e){
        e.preventDefault()
        const data = {
        "password": this.password,
        "identifiant": this.room,
        "admin":this.$store.getters.getConnexion.authStore.model.id
        };
        console.log(data);

    const record =  this.$store.getters.getConnexion.collection('room').create(data);
    console.log(record);
    record.then((result)=>{
        this.message = true
        this.created = true
        this.serveur = result
    })
    .catch((error)=>{
        console.log(error);
    })
    },
    joinServeur(){
        this.$router.push(`/diffusion/${this.serveur.id}/${this.serveur.identifiant}`)
    }
  }
};
</script>
