import { createRouter, createWebHistory } from "vue-router";
// import IsAuthenticated from "@/helpers/AuthUser";

import HomeViewVue from "@/views/HomeView";
import JoinServerView from "@/views/JoinServerView";
import CreationServerView from "@/views/CreationServerView";
import DiffusionView from '@/views/DiffusionView'
import LoginSignInView from "@/views/LoginSignInView";
import LoginSignUpView from "@/views/LoginSignUpView";

const routes = [
  {
    path: "/",
    name: "Home page",
    component: HomeViewVue,
    meta: {
      requiresAuth: false,
    },
  },
  {
    path: "/join-server",
    name: "join server page",
    component: JoinServerView,
    meta: {
      requiresAuth: true,
    },
  },
  {
    path: "/creation-server",
    name: "Creation server page",
    component: CreationServerView,
    meta: {
      requiresAuth: true,
    },
  },
  {
    path: "/login",
    name: "login route",
    meta: {
      requiresAuth: false,
    },
    children: [
      {
        path: "sign-in",
        name: "sign-in route",
        component: LoginSignInView,
        meta: {
          requiresAuth: false,
        },
        alias: ["sign-in"],
      },
      {
        path: "sign-up",
        name: "sign-up route",
        component: LoginSignUpView,
        meta: {
          requiresAuth: false,
        },
      },
    ],
  }, 
  {
    path:'/diffusion/:roomId/:roomIdentifiant',
    name:'Diffusion page',
    component:DiffusionView,
    meta:{
      requiresAuth:true
    }
  }
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

